#home {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
#home .rightcontent {
  padding: 40% 0px;
}
#home .imgsec {
  position: relative;
}
#home .imgsec img {
  left: 150px;
  position: inherit;
  z-index: 3;
  top: 50px;
  border-radius: 5px;
}
#home .imgsec .bgblack {
  background-color: #000;
  position: absolute;
  width: 90%;
  height: 101vh;
  top: -11%;
  left: -10vh;
  z-index: 1;
}
#home h1::before {
  content: "";
  border-left: 3px solid #000;
  left: -30px;
  top: 0px;
  position: absolute;
  height: 200%;
