  z-index: 999;
  border-left: 0px;
}

#home .socialmedia::after {
  content: "";
  /* border-bottom: 3px solid #000; */
  position: absolute;
  width: 150%;
  bottom: 0;
  right: 0;
  height: 3px;
  /* animation: opacityani 3s; */
