html,body{padding: 0px !important;margin: 0px !important;overflow:hidden;}
h1{font-size: 50px !important;font-family: 'Montserrat', sans-serif;font-weight: 900 !important;}
h2{font-size: 30px !important;font-family: 'Montserrat', sans-serif;font-weight: 600 !important;margin-bottom: 15px !important;}
h4{font-family: 'Montserrat', sans-serif;font-size: 20px !important;font-weight: 900 !important;}
h5{font-size: 22px !important;font-weight: 600 !important;font-family: 'Montserrat', sans-serif;margin-bottom: 10px !important;line-height: 30px !important;}
h6{font-size: 18px !important;font-weight: 600 !important;font-family: 'Montserrat', sans-serif;}
.btn{background-color: transparent !important;color:#000 !important;border-radius: 0px !important;border-color: #000 !important;border-width: 2px !important;padding: 8px 40px !important;font-family: 'Montserrat', sans-serif;font-size: 16px !important;font-weight: 600 !important;text-transform: uppercase;transition: 0.4s ease-in !important;letter-spacing: 1px;}
.btn:hover{background-color: #000 !important;color: #fff !important;}
p{font-family: 'Montserrat', sans-serif;letter-spacing: 0.2px;font-size: 20px !important;}

@media (max-width:767px){
    html,body{overflow-y: visible;}
}
#about{height: 100vh;}
#about .aboutcontent{border:2px dashed #000; display: flex;justify-content: center;align-items: center;  min-height: 60vh;margin-top: 20vh;flex-direction: column;text-align: center;padding: 20px;position: relative;background-color: #fff;}
#about .aboutcontent::before{content: '';position: absolute;top: -25px;left: -25px;width:50px;height: 50px;background-color: #000;-webkit-animation:rotate 3s ease infinite;animation:rotate 3s ease infinite;}
#about .aboutcontent::after{content: '';position: absolute;bottom: -12.5px;right: -12.5px;width:25px;height: 25px;background-color: #000;-webkit-animation:rotate 3s ease infinite;animation:rotate 3s ease infinite;}
@-webkit-keyframes rotate{
    25%{
        -webkit-transform: rotate(90deg);
                transform: rotate(90deg);
        -webkit-transform: scale(0.7);
                transform: scale(0.7);
    }
    50%{
        -webkit-transform: rotate(180deg);
                transform: rotate(180deg);
    }
    75%{
        -webkit-transform: rotate(270deg);
                transform: rotate(270deg);

    }
    100%{
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);

    }
}
@keyframes rotate{
    25%{
        -webkit-transform: rotate(90deg);
                transform: rotate(90deg);
        -webkit-transform: scale(0.7);
                transform: scale(0.7);
    }
    50%{
        -webkit-transform: rotate(180deg);
                transform: rotate(180deg);
    }
    75%{
        -webkit-transform: rotate(270deg);
                transform: rotate(270deg);

    }
    100%{
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);

    }
}

#services{overflow: scroll;height: 100vh; padding-top: 100;}
#services .servicebox{text-align: center;background-color: rgb(255, 255, 255);border-radius: 20px;padding:30px 5px 40px;transition: 0.4s linear;border: 5px double #fff;margin-bottom: 25px;border-color: #000;}
#services h4{color:#000;}
#services img{width: 30%;margin: 30px 0px;}
#services .servicebox:hover{background-color: #fff;border:5px double #000;-webkit-transform: scale(1.02);transform: scale(1.02);}
#services .servicebox:hover h4{color:#000;}
#services .servicebox:hover img{-webkit-filter: none;filter: none;}

