#contact{height: 100vh;display: flex;justify-content: center;align-items: center;background-color: #000;color:#fff;text-align: center;}
.form-control{background-color: rgb(18, 18, 18) !important;border:none !important;border-radius: 0px !important;margin-bottom: 15px;border-bottom: 1px solid rgb(23, 23, 23) !important;color:#fff !important;}
.form-control:focus{box-shadow: none !important;border-color:rgba(255,255,255,0.2) !important;}
#contact .btn{background-color: #fff !important;}
#contact .btn:hover{background-color: #000 !important;color:#fff !important;border-color :#fff !important;}
#portfolio{height: 100vh;overflow: scroll;padding: 10vh 0px;}
#portfolio .imgsec{height: 40vh;margin-bottom: 5vh;overflow: hidden;border: 3px solid #000;border-top: 30px solid #000;position: relative;}
#portfolio .imgsec:hover img{-webkit-animation: portfolioani 7s linear ;animation: portfolioani 7s linear ;}
#portfolio .imgsec:hover{-webkit-transform:scale(1.03);transform:scale(1.03);transition: 1s ease;outline: 3px solid #000;}
@-webkit-keyframes portfolioani{
    0%{
        -webkit-transform: translateY(0%);
                transform: translateY(0%);
    }
    100%{
        -webkit-transform: translateY(-100%);
                transform: translateY(-100%);
    }
   
}
@keyframes portfolioani{
    0%{
        -webkit-transform: translateY(0%);
                transform: translateY(0%);
    }
    100%{
        -webkit-transform: translateY(-100%);
                transform: translateY(-100%);
    }
   
}
